import React from "react";
import {MDBBtn} from "mdbreact";
import {useHistory} from 'react-router-dom';
import './OurResearch.scss';

const OurResearch = () => {
  let history = useHistory();

  return (<div class="container">

    <section class="px-md-5 mx-md-5 dark-grey-text">

      <h1 className="h1-responsive font-weight-bold my-5 text-center">
        
      </h1>

      <p className="lead mx-auto mb-3">
        
      </p>
      <div >
        <ol className="custom-counter" style={{
            marginLeft: "20px"
          }}>
          <li className="text-primary">
            <a onClick={() => {
                history.push('/Research')
                window.scrollTo(0, 0)
              }}> (Treatment Resistant Depression: TRD)</a>
          </li>
          <li className="text-primary">
            <a onClick={() => {
                history.push('/Research')
                window.scrollTo(0, 0)
              }}> / (Life-threatening Distress in Palliative Diagnosis)</a>
          </li>
          <li className="text-primary">
            <a onClick={() => {
                history.push('/Research')
                window.scrollTo(0, 0)
              }}> (Major Depressive Disorder: MDD)</a>
          </li>
          <li className="text-primary">
            <a onClick={() => {
                history.push('/Research')
                window.scrollTo(0, 0)
              }}> (Generalized Anxiety Disorder: GAD)</a>
          </li>
          <li className="text-primary">
            <a onClick={() => {
                history.push('/Research')
                window.scrollTo(0, 0)
              }}> (Obsessive Compulsive Disorder: OCD)</a>
          </li>
          <li className="text-primary">
            <a onClick={() => {
                history.push('/Research')
                window.scrollTo(0, 0)
              }}> Post-Traumatic Stress Disorder (PTSD)</a>
          </li>
          <li className="text-primary">
            <a onClick={() => {
                history.push('/Research')
                window.scrollTo(0, 0)
              }}> (Smoking Cessation)</a>
          </li>
          <li className="text-primary">
            <a onClick={() => {
                history.push('/Research')
                window.scrollTo(0, 0)
              }}> (Healthy Human)</a>
          </li>
        </ol>
      </div>
      <p className="lead mx-auto mb-5 mt-4">
            (Clinical Trials)  38  (Psilocybin) 
      </p>

      <center>
        <MDBBtn onClick={() => {
            history.push('/Research')
            window.scrollTo(0, 0)
          }} className="btn-mix font-weight-bold" rounded color="primary" style={{fontSize:"14px"}} data-text=""></MDBBtn>

      </center>
    </section>
  </div>)
}
export default OurResearch
